export default {
    name: 'Header',

    computed: {
        tab() {
            return this.$route.path.split('/')[1]
        }
    },

    watch: {
        tab: {
            handler() {
                if (this.displayMenu) this.displayMenu = false;
            }
        }
    },

    data() {
        return {
            isDarkMode: true,
            displayMenu: false
        }
    },

    methods: {
        toggleTheme() {
            this.isDarkMode = !this.isDarkMode;

            let theme = this.isDarkMode ? 'darkMode' : '';
            document.documentElement.setAttribute('data-theme', theme); 
            localStorage.setItem('theme', theme); 
        },

        toggleMenu() {
            this.displayMenu = !this.displayMenu;
        },

        openInstagram() {
            window.open('https://www.instagram.com/guillaume.laville', '_blank').focus();
        }
    },

    mounted() {
        document.documentElement.setAttribute('data-theme', this.isDarkMode ? 'darkMode' : ''); 
    }
}