import { createRouter, createWebHistory } from 'vue-router'

const routes = [
  {
    path: '/',
    name: 'Index',
    redirect: '/gouache'
  },
  {
    path: '/gouache',
    name: 'GouacheIndex',
    component: () => import('../views/gouache/gouache.index.vue')
  },
  {
    path: '/gouache/:gouacheId',
    name: 'GouacheDetail',
    component: () => import('../views/gouache/gouache.detail.vue'),
    props: true
  },
  {
    path: '/music',
    name: 'MusicIndex',
    component: () => import('../views/music/music.index.vue')
  },
  {
    path: '/user',
    name: 'UserIndex',
    component: () => import('../views/user/user.index.vue'),
  },
  {
    path: '/panier',
    name: 'PanierIndex',
    component: () => import('../views/panier/panier.index.vue'),
  },
  {
    path: '/artist',
    alias: '/artiste',
    name: 'ArtistIndex',
    component: () => import('../views/artist/artist.index.vue'),
  },
  {
    path: '/:pathMatch(.*)*',
    redirect: '/'
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
